
.swiper-box {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.swiper-item {
  height: 300px;
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.tipsbox {
  top: .4rem;
  left: -.2rem;
  z-index: 2
}
.el-icon-search {
  position: absolute;
  height: .5rem;
  width: .5rem;
  font-size: .4rem;
  line-height: .5rem;
  color: #45C3D3;
}
.search-box {
  /*box-shadow: 0px 0px 10px #eee inset;*/
}



