
.ordertipsnum {
  display: block;
  width: .4rem;
  height: .4rem;
  background: red;
  color: #fff;
  font-size: .22rem;
  border-radius: 50%;
  line-height: .4rem;
  text-align: center;
  position: absolute;
  top: 0.06rem;
  z-index: 999;
  right: 1.1rem;
}
.header-line {
  padding-top: .8rem;
  margin-top: -1px;
}
.Router {
  position: absolute;
  width: 100%;
  -webkit-transition: all .8s ease;
  transition: all .8s ease;
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}



